.swipeButtons{
    position:fixed;
    bottom: 4vh;
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.swipeButtons .MuiIconButton-root{
    background-size: 0;
    background-color: white;
    box-shadow: 0px 10px 53px rgba(0,0,0,0.3) !important;
}

.swipeButtons_repeat{
    padding: 3vw !important;
    color: #f5b748 !important;
}

.swipeButtons_left{
    padding: 3vw !important;
    color: #ec5e6f !important;
}

.swipeButtons_star{
    padding: 3vw !important;
    color: #62b4f9 !important;
}

.swipeButtons_right{
    padding: 3vw !important;
    color: #77ec18 !important;
}

.swipeButtons_lightning{
    padding: 3vw !important;
    color: #9b44b6 !important;
}